/*Header active nav icon added*/
nav .ACTIVENAV{
	position: relative;
}
nav .ACTIVENAV:before{	
	background-image: url('../images/ktktlogo.png');
	content: "";
	height: 40px;
	width: 19px;
	right: 0;
	top: -14px;
	position: absolute;
	background-size: contain;
	background-repeat: no-repeat;
}

.main-logo img{
	width: 120px;
}
/*Header Setting icons added*/
#open-settings-dialog-button:before{
	background-image: url('../images/cogs.png');
